@import url(https://fonts.googleapis.com/css?family=Roboto|Shrikhand);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  /* 12px */
  font-size: 75%;

  --grey-100: #fffbf9;
  --grey-300: #e7e3e0;
  --grey-500: #b0b0b0;
  --grey-700: #797470;
  --grey-900: #201e1d;

  --orange-100: rgb(255, 217, 205);
  --orange-300: rgb(245, 171, 148);
  --orange-500: rgb(243, 126, 90);
  --orange-700: rgb(204, 105, 74);
  --orange-900: rgb(131, 60, 38);

  --purple-100: rgb(190, 183, 226);
  --purple-300: rgb(110, 100, 163);
  --purple-500: rgb(52, 44, 95);
  --purple-700: rgb(31, 26, 61);
  --purple-900: rgb(6, 5, 12);

  --green-300: rgb(18, 221, 126);
  --green-700: rgb(11, 148, 84);

  --red-500: rgb(228, 86, 67);
  --red-700: rgb(172, 54, 39);
}

p,
a,
h1,
h2,
h3,
h4,
h5,
ul {
  margin: 0;
  padding: 0;
}

p,
a {
  line-height: 1.5;
}
@media (min-width: 30rem) {
    .query-container {
      margin: 2rem;
    }
  }
  
.books-container {
    margin: 1rem;
    overflow-y: scroll;
    box-shadow: 1rem 1.5rem var(--purple-300);
    background-color: var(--orange-100);
    border: .2rem solid var(--purple-300);
}

.Home {
    display: grid;
    margin: 0;
    padding:0;
    background-color:var(--grey-300);
    height: 100vh;
    overflow-x: auto;
    font-size: 1.5rem; /* 18px */
}
  
@media (min-width: 60rem) {
    .Home {
        grid-template-columns: 1fr 2fr;
    }
}
.query-title {
    font-family: 'Shrikhand', cursive;
    font-size: 5rem;
    color: var(--purple-500);
    margin-bottom: 0;
    margin-left: 1rem;
}

input[type=text], select {
    width: 100%;
    padding: 1rem 1rem;
    margin: 0;
    display: inline-block;
    border: .1rem solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 2rem;
}

.query-input:focus {
    outline: none;
    box-shadow: none;
    border: 1px solid var(--orange-500);
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px white inset;
}

.query-btn {
    width: 100%;
    background-color:var(--orange-500);
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 2rem;
}

.query-btn:hover {
    background-color: var(--orange-700);
}

.query-btn:focus {
    outline:0;
}
.result-box {
    font-family: 'Roboto', sans-serif;
    background-color: var(--grey-100);
    margin: 2rem;
    padding: 2rem;
    border-radius: .5rem;
    box-shadow: 0px 0px 23px 3px var(--orange-300);
    display:grid;
     
}

@media (min-width: 40rem) {
    .result-box {
        display:grid;
        grid-template-columns: 2fr 1fr; 
    }
}

.title {
    color: var(--purple-700);
    text-decoration: none;
}

.publisher {
    font-style: italic;
}

.search-snippet {
   margin: .5rem;
   color: var(--grey-900);
}

.result-image {
    margin: .5rem;
}

ul {
    list-style-type: none;
}

li {
    display: inline;
    padding-right: .5rem;
}

a {
    color: var(--orange-700);
}

a:hover {
    color: var(--orange-500);
}

